import React from "react";
import { SliderData } from "../Slider/SliderData";
import "./fwcup.scss";

const Fwcup = (slide) => {
  // const limit=2
  return (
    <div className="world-cup">
      <div className="wc-text">
        <h1>FIFA WORLD CUP QATAR 2022™ QUALIFIERS.</h1>
        <p>
          Here you will find all the information about the road to Qatar 2022™
          as the tournament looms ever closer.
        </p>
        <button className="btn-more">All News</button>
      </div>
      <div className="slide-card">
        {SliderData.slice(0, 2).map((slide, index) => (
          <div className="slide" key={slide.id}>
            <img src={slide.Image} alt="card" />
            <div className="slide-text">
              <h5>Category</h5>
              <h1>{slide.Title}</h1>
              <button className="btn-more">More</button>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Fwcup;
