import React from "react";
import { FaArrowRight } from "react-icons/fa";
import { SliderData } from "../Slider/SliderData";
import "./competitions.scss";

const Competions = () => {
  return (
    <div className="comp-news">
      <h1>News from fufa competitions</h1>
      <hr />
      <div className="stories">
        {SliderData.map((slide, index) => (
          <div className="post" key={slide.id}>
            <img src={slide.Image} alt="post" />
            <div className="post-text">
              <h1>{slide.Title}</h1>
              <button className="btn-more">
                <FaArrowRight />
              </button>
            </div>
          </div>
        ))}
      </div>
      <button className="btn-more"style={{margin:" 10px 0",border:"2px solid #343b92"}}>
        More news
        <FaArrowRight style={{margin:"0 10px"}}/>
      </button>
    </div>
  );
};

export default Competions;
