import React from 'react'
import { Link } from 'react-router-dom'

const FootLinks = (props) => {
    return (
        <div className='footlink'>
            <h1>{props.title}</h1>
            <ul className="footer-nav">
                <Link to={props.link}>
                    <p>{ props.navlink}</p>
                </Link> <Link to={props.link}>
                    <p>{ props.navlink}</p>
                </Link> <Link to={props.link}>
                    <p>{ props.navlink}</p>
                </Link> <Link to={props.link}>
                    <p>{ props.navlink}</p>
                </Link>
</ul>
        </div>
    )
}

export default FootLinks
