import React from "react";
import "./women.scss";
import { SliderData } from "../Slider/SliderData";

const Women = (slide) => {
  return (
    <div className="women">
      <div className="women-text">
        <h1>women in football</h1>
        <p>All you need to know about the female game</p>
        <button className="btn-more">Find more..</button>
      </div>
      <div className="slide-card">
        {SliderData.slice(0, 2).map((slide, index) => (
          <div className="slide" key={slide.id}>
            <img src={slide.Image} alt="card" />
            <div className="slide-text">
              <h5>Category</h5>
              <h1>{slide.Title}</h1>
              <button className="btn-more">More</button>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Women;
