import React from "react";
import "./footer.scss";
import FootLinks from "./FootLinks";
import Partners from "./Partners";

const Footer = () => {
  return (
    <div className="footer">
      <Partners />
      <div className="bfooter">
      <FootLinks title="Company" link="/about" navlink="About"/>
      <FootLinks title="Company" link="/about" navlink="About"/>
      <FootLinks title="Company" link="/about" navlink="About"/>
      <FootLinks title="Company" link="/about" navlink="About"/></div>
    </div>
  );
};

export default Footer;
