import React, { useState } from "react";
import Logo from "./Logo";
import "./navbar.scss";
import MenuItem from "./MenuItem";
import {
  FaAward,
  FaBars,
  FaCartPlus,
  FaCloudMeatball,
  // FaFootballBall,
  FaHome,
  FaPhoneAlt,
  // FaTh,
  FaTimes,
  FaUsers,
} from "react-icons/fa";
import MegaAbout from "../Megamenu/MegaAbout";
// import Megamenu from "../Megamenu/Megamenu";

const Navbar = () => {
  const [mobile, setMobile] = useState(true);
  const Toggle = () => {
    setMobile(!mobile);
  };
  return (
    <div className="navbar">
      <Logo />
      {mobile && (
        <ul className="navbar-nav">
          <MenuItem icon={<FaHome />} link="Home" path="/" />
          <MenuItem
            icon={<FaCloudMeatball />}
            link="About"
            path="/about"
            comp={<MegaAbout />}
          />

          <MenuItem
            icon={<FaAward />}
            link="Competitions"
            path="/competitions"
          />
          <MenuItem icon={<FaUsers />} link="Teams" path="/teams" />
          <MenuItem icon={<FaCartPlus />} link="Shop" path="/shop" />
          <MenuItem icon={<FaPhoneAlt />} link="Contact" path="/contact" />
          <MegaAbout />
        </ul>
      )}

      <h2 className="toggler" onClick={Toggle}>
        {mobile ? <FaTimes /> : <FaBars />}
      </h2>
    </div>
  );
};

export default Navbar;
