import React from "react";
import Men from "../../Images/rank.png";
import women from "../../Images/womenrank.png";
import "./Ranking.scss";

const Ranking = () => {
  return (
    <div className="ranking">
      <div className="rank-text">
        <h1>fifa world rankings</h1>
        <p>
          The official world rankings of the international mens and womens
          teams.
        </p>
        <button className="btn-more">See full list</button>
      </div>
      <div className="rank">
        <div className="ranks">
          <h1>Mens' national team ranking</h1>
          <img src={Men} alt="" />
        </div>{" "}
        <div className="ranks">
          <h1>Womens' national team ranking</h1>
          <img src={women} alt="" />
        </div>
      </div>
    </div>
  );
};

export default Ranking;
