import React from "react";
import Home from "../../Images/home.png";
import Kit from "../../Images/new.png";
import Away from "../../Images/away.png";

const Players = () => {
  return (
    <div className="players">
      <div className="player-card">
        <img src={Home} alt="Home kit" />
        <div className="player-text">
          <h1>
            <span>12</span>John Doe
          </h1>
          <p>Forward</p>
        </div>
      </div>{" "}
      <div className="player-card">
        <img src={Away} alt="new kit" />
        <div className="player-text">
          <h1>
            <span>9</span>Jane Doe
          </h1>
          <p>Defender</p>
        </div>
      </div>{" "}
      <div className="player-card">
        <img src={Kit} alt="away kit" />
        <div className="player-text">
          <h1>
            <span>1</span> Doe John
          </h1>
          <p>Midfielder</p>
        </div>
      </div>
    </div>
  );
};

export default Players;
