import React from "react";
import Navbar from "./Components/Navbar/Navbar";
import { BrowserRouter as Router } from "react-router-dom";
import Slider from "./Components/Slider/Slider";
import { SliderData } from "./Components/Slider/SliderData";
import Fwcup from "./Components/Fwcup/Fwcup";
import Competions from "./Components/Competitions/Competions";
// import Scroll from "./Components/Scroll";
import Ranking from "./Components/Rankings/Ranking";
import Women from "./Components/Women/Women";
import WomenList from "./Components/Women/WomenList";
import Shoping from "./Components/Shop/Shoping";
import Cranes from "./Components/Cranes/Cranes";
import Footer from "./Components/Footer/Footer";

const App = () => {
  return (
    <Router>
      <div className="wrapper-main">
        <Navbar />
        <Slider slide={SliderData} />
        <Fwcup />
        <Cranes />
        <Ranking />
        <Women />
        <WomenList />
        <Shoping />
        <Competions />

        <Footer />
      </div>
    </Router>
  );
};

export default App;
