import React from "react";
import Airtel from "../../Images/airtel.png";
import Nic from "../../Images/download.png";
import Nile from "../../Images/nile.jpg";
import Fufa from "../../Images/fufa.jpg";
import Bul from "../../Images/picture.png";
import Eco from "../../Images/eco.png";
import Star from "../../Images/star.png";
import Stan from "../../Images/stan.jfif";

const Partners = () => {
  return (
    <div className="partners">
      <h1>our partners and sponsors</h1>
      <div className="logos">
        <img src={Airtel} alt="logos" />
        <img src={Nic} alt="logos" />
        <img src={Nile} alt="logos" />
        <img src={Bul} alt="logos" />
        <img src={Eco} alt="logos" />
        <img src={Fufa} alt="logos" />
        <img src={Star} alt="logos" />
        <img src={Stan} alt="logos" />
      </div>
    </div>
  );
};

export default Partners;
